import React, { Component } from 'react'
import axios from 'axios'
import ReactHtmlParser from 'react-html-parser'

const host = 'https://prudential-helper.herokuapp.com'

class BookDetails extends Component {
  constructor (props) {
    super(props)
    this.state = { ...this.props.location.state }
    this.state = { ...this.state, description: null}
  }

  componentDidMount () {
    this.getBookDescription()
  }

  getBookDescription () {
    const { id } = this.props.match.params
    axios.get(`${host}/bookdescription/${id}`)
    .then(response => this.setState({ description: response.data }))
    .catch (err => console.error(err))
  }

  render () {
    const { title, author, imageSrc, averageRating,
      reviewsCount, ratingsCount, description } = this.state
    return (
      <div className="row">
        <div className="col-sm-1">
          <img src={imageSrc}/>
        </div>
        <div className="col-sm-3">
          <h2>{title}</h2>
          <h3>By {author}</h3>
          <h4>Rating: {averageRating}</h4>
          {
            description !== null
            ? <p>description: {ReactHtmlParser(description)}</p>
            : <p>Loading...</p>
          }
        </div>
      </div>
    )
  }
}

export default BookDetails
