import React, { Component } from 'react'
import axios from 'axios'
import { Link } from 'react-router-dom'

const host = 'https://prudential-helper.herokuapp.com'

export default class BookStore extends Component {
  constructor (props) {
    super(props)
    this.state = {
      search: '',
      results: []
    }
    this.onSearchChange = this.onSearchChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }

  onSearchChange (ev) {
    const search = ev.target.value
    this.setState({ search })
  }

  handleSubmit (ev) {
    ev.preventDefault()
    this.getBooksData(this.state.search)
  }

  getBooksData (query) {
    axios(`${host}/search?q=${query}`)
    .then(response => this.setState({ results: response.data }))
    .catch(err => console.error(err))
  }

  render () {
    return (
      <>
        <form onSubmit={this.handleSubmit} className='row'>
          <input
            type='text'
            placeholder='Search Books'
            value={this.state.search}
            onChange={this.onSearchChange}
            className='col-sm-offset-3 col-sm-5'
          />
          <button className='btn btn-primary col-sm-3'>Search</button>
        </form>
        {
          this.state.results.map(result => (
            <li key={result.key}>
              <Link to={{
                pathname: `/books/${result.id}`,
                state: result
              }}>{result.title}</Link>
            </li>
          ))
        }
      </>
    )
  }
}
